	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 0px;
	width: 800px;
	
	border: 5px dashed #ffba00;
	border-radius: 18px;
}

.winner-row {
display: flex;
justify-content: flex-start;
gap: 40px;
background-color: black;
  color: #ffba00;
  border-bottom: 4px dashed #ffba00;
  padding: 20px 5px 20px 0;
  
}

