___CSS_LOADER_EXPORT___.push([module.id, `.car-container {
	display: flex;
	flex-direction: column;
	gap: 18px;
	width: 100%;
	border: 1px solid white;
	border-radius: 14px;
	background: black;
	box-shadow: 0 4px 10px rgb(0 0 0 / 90%);
	padding: 20px 0 20px 0;
}

.road-container {
	display: flex;
	position: relative;
	width: 100%;
	height: 100px;
	border-bottom: 2px dashed white;
	padding: 0 0 0 38px;
	align-items: center;
	gap: 20px;
	overflow: hidden;
}

.start-race {
	width: 30px;
	height: 30px;
	cursor: pointer;
	background: #2a3132;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	background-position: center;
	background-size: contain;
}

.start-race:hover {
	transform: scale(1.05);
}

.start-race:disabled {
	pointer-events: none;
	cursor: not-allowed;
	opacity: 0.5;
}

.start-race.stop-race {
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.car {
	width: 80px;
	height: 80px;
	background: #2a313200;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
	background-position: center;
	background-size: contain;
	position: absolute;
	left: 140px;
	top: 50%;
	transform: translateY(-50%);
}

.car-name {
	display: flex;
	align-items: center;
	white-space: nowrap;
	font-family: "Audiowide", sans-serif;
	font-weight: 400;
	font-style: normal;
	color: #ffba00;
}

.finish {
	width: 80px;
	height: 80px;
	background: #2a313200;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
	background-position: center;
	background-size: contain;
	position: absolute;
	right: 40px;
	top: 50%;
	transform: translateY(-50%);
}
